.discounts {
  padding-top: 63px;
  padding-bottom: 70px;
  text-align: center;

  h2 {
    font-family: $ff-alumniSans-semibold;
    font-size: 32px;
    line-height: 36px;
    letter-spacing: 1px;
    text-align: center;
    width: 100%;
    max-width: 550px;
    margin: 0;
    margin-bottom: 41px;
    color: $color-sapphire;
  }

  h3 {
    font-family: $ff-regular;
  }

  p {
    font-family: $ff-regular;
    margin: 0;
  }

  a {
    width: fit-content;
    font-family: $ff-regular;
    text-transform: uppercase;
    border-bottom: 1px solid;
    padding: 15px 0 2px;
    letter-spacing: 0.5px;
    margin: 0 auto;

    &:hover,
    &:focus {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.discounts__wrapper {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto auto auto;
  color: $color-sapphire;
  text-align: center;
  gap: 30px;
}

.discounts__unlimited {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  color: $color-default-white;
}

.discounts__unlimited-info {
  display: flex;
  flex-direction: column;
  background-color: $color-torch-red;
  padding: 44px 40px 45px 41px;

  span {
    font-family: $ff-regular;
    font-size: 70px;
    line-height: 70px;
    margin-bottom: 20px;
    font-weight: 700;
    margin-bottom: 30px;
  }

  h3 {
    font-size: 28px;
    line-height: 23px;
    margin: 0;
    margin-bottom: 19px;
  }

  p {
    font-size: 18px;
    line-height: 24px;
    margin-bottom: 40px;
  }

  a {
    font-size: 14px;
    line-height: 13px;
    color: $color-default-white;
    border-color: $color-default-white;
  }
}

.discounts__unlimited-image {
  width: 100%;
  height: 299px;
  overflow: hidden;

  img {
    object-fit: cover;
    object-position: 50% 90%;
    width: 100%;
    height: 100%;
  }
}

.discounts__block {
  display: flex;
  flex-direction: column;
  min-height: 211px;
  height: auto;
  padding: 23px 30px 27px;
  border: 7px solid;
  border-image: $gradient;
  border-image-slice: 1;

  h3 {
    font-size: 26px;
    margin: 0;
    margin-bottom: 20px;
  }

  p {
    font-size: 16px;
    margin: 0;
    margin-bottom: 35px;
  }

  a {
    font-size: 12px;
    color: $color-sapphire;
    border-color: $color-sapphire;
  }
}
