.gym {
  display: grid;
  grid-template-columns: 100%;
  gap: 22px;
  background-color: $color-default-white;
  color: $color-sapphire;
  padding-top: 70px;
  padding-bottom: 70px;
}

.gym__info {
  padding: 0;

  h2 {
    font-family: $ff-alumniSans-semibold;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.96px;
    margin: 0;
    margin-bottom: 8px;
  }

  span {
    display: block;
    font-family: $ff-regular;
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 39px;
  }

  p {
    font-family: $ff-light;
    font-size: 16px;
    line-height: 24px;
    margin: 0 0 8px;
  }
}

.gym__view {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 40px;
  width: 100vw;
  margin-left: -15px;
}

.gym__photo {
  overflow: hidden;
  width: 320px;
  height: 313px;
  /* margin-left: 63px;*/
  /* margin-bottom: 95px; */

  img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.gym__video {
/*   position: absolute;
  left: 0;
  bottom: -20px; */
  position: relative;
  width: 100%;
  height: 167px;

  iframe {
    width: 100%;
    height: 100%;
  }
}

.gym__video-rpeview {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: no-repeat center / cover url("../img/video_preview.jpg");
}

.gym__video-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100px;
  height: 100px;
  background: no-repeat center / contain url("../img/play_icon.svg") transparent;
  border-radius: 50%;
  cursor: pointer;
  border: none;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

