.advantages {
  background-color: $ghost-white;
  padding-top: 115px;
  padding-bottom: 116px;
  color: $color-sapphire;
  padding-top: 63px;
  padding-bottom: 70px;

  h2 {
    font-family: $ff-alumniSans-semibold;
    text-align: center;
    width: fit-content;
    color: $color-sapphire;
    max-width: 550px;
    margin: 0 auto 65px;
    font-size: 32px;
    line-height: 36px;
    letter-spacing: 1px;
    margin-bottom: 46px;
  }
}

.advantages__wrapper {
  display: flex;
  flex-direction: column;
  gap: 0;
  padding: 0;
  gap: 0 60px;
  padding-left: 164px;
  padding-right: 164px;
}

.advantages__block {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
  align-items: center;
  padding: 0 46px;

  div:first-child {
    display: flex;
    flex-direction: column;
    margin-bottom: 10px;

    span:first-child {
      font-family: $ff-regular;
      font-size: 71px;
      line-height: 55px;
      color: transparent;
      background-clip: text;
      background-image: $gradient;
      margin: 0 30px 1px 0;
      margin: 0;
    }

    span:last-child {
      font-family: $ff-regular;
      font-size: 14px;
      line-height: 21px;
      color: $persian-blue;
    }
  }

  h3 {
    font-family: $ff-regular;
    font-size: 22px;
    line-height: 20px;
    margin: 0 0 12px;
  }

  p {
    font-family: $ff-light;
    font-size: 16px;
    line-height: 22px;
    margin: 0;
  }

  &:not(:last-child) {
    display: flex;
    flex-direction: column;
  }
}

.advantages__center {
  padding: 54px 34px 62px;
  grid-area: b;
  color: $ghost-white;
  background-color: $resolution-blue;
  text-align: center;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 45px;
  padding: 50px 74px 43px;

  span {
    display: block;
    font-family: $ff-regular;
    font-size: 74px;
    line-height: 74px;
    margin-bottom: 6px;
  }

  h3 {
    font-family: $ff-regular;
    font-size: 26px;
    line-height: 26px;
    margin: 0 0 39px;
    margin-bottom: 9px;
  }

  p {
    font-family: $ff-regular;
    font-size: 16px;
    line-height: 24px;
    margin: 0;
  }
}

  .advantages__wrapper {
    display: flex;
    flex-direction: column;
    gap: 0;
    padding: 0;
  }

  .advantages__center {
    width: 100%;
    margin-top: 5px;
    margin-bottom: 45px;
    padding: 50px 74px 43px;

    span {
      margin-bottom: 6px;
    }

    h3 {
      margin-bottom: 9px;
    }
  }
