.questions {
  background-color: $ghost-white;
  padding-top: 115px;
  padding-bottom: 116px;
  color: $color-sapphire;
  padding-top: 70px;
  padding-bottom: 64px;

  h2 {
    font-family: $ff-alumniSans-semibold;
    text-align: center;
    width: fit-content;
    max-width: 550px;
    margin: 0 auto;
    margin-bottom: 36px;
    text-align: center;
    font-size: 32px;
    line-height: 36px;
    letter-spacing: 1px;
  }
}

.questions__tabs {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
  padding-bottom: 30px;
  border-bottom: 1px solid rgba(16,	38,	84, 0.2);
}

.questions__accordion-element.is-active span {
  background-color: $resolution-blue;
}

.questions__accordion-button {
  display: flex;
  align-items: center;
  gap: 20px;
  justify-content: space-between;
}

.questions__tab-button.is-active {
  background: $button-gradient;
  color: $color-default-white;
  border: none;
}

.questions__accordion-element {
  padding-top: 20px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(16,	38,	84, 0.2);

  & p {
    color: $color-sapphire;
    font-family: $ff-regular;
    font-size: 16px;
    line-height: 22px;
    font-weight: 300;
    margin: 0;
  }
}
