.subscript {
  background-color: $ghost-white;
  color: $color-sapphire;
  text-align: center;
  padding-top: 68px;
  padding-bottom: 70px;

  h2 {
    display: block;
    max-width: 1100px;
    font-family: $ff-alumniSans-semibold;
    font-size: 49px;
    line-height: 55px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    margin: 0 auto 23px;
  }
}

.subscript__pane {
  width: 100%;
}

.subscript__tabs {
  position: relative;
  z-index: 1;
}

.subscript__nav {
  display: flex;
  gap: 45px;
  width: fit-content;
  margin: 0 auto 38px;
  border-bottom: 2px solid rgba(120, 120, 120, 0.2);
}

.subscript__tab {
  position: relative;
  font-family: $ff-regular;
  font-size: 20px;
  line-height: 30px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  color: rgba(16, 38, 84, 0.5);
  padding: 10px 0;

  &.is-active {
    cursor: default;
    color: $color-sapphire;
  }

  &.is-active::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $color-torch-red;
    bottom: -2px;
    left: 0;
  }

  &:not(.subscript__tab--active):hover {
    color: $color-sapphire;
    opacity: 0.9;
  }

  &:not(.subscript__tab--active):focus {
    color: $color-sapphire;
    opacity: 0.9;
  }
}

.subscript__pane {

  ul {
    display: grid;
    grid-template-columns: 1fr;
    gap: 40px;
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  li {
    position: relative;
    box-sizing: border-box;
    background-color: $color-default-white;
    color: $color-sapphire;
    padding: 57px 30px 68px;
    z-index: 1;
  }

  h3 {
    font-family: $ff-regular;
    font-size: 26px;
    line-height: 40px;
    margin: 0 0 5px;
  }

  span {
    display: block;
    font-family: $ff-regular;
    font-size: 16px;
    line-height: 24px;
    margin-bottom: 80px;
  }

  div {
    display: flex;
    align-items: center;
    width: fit-content;
    margin: 0 auto 49px;
  }

  p {
    width: fit-content;
    font-family: $ff-regular;
    font-size: 60px;
    line-height: 60px;
    color: $color-egyptian-blue;
    margin: 0 14px 0 0;

    &::before {
      content: attr(data-price);
      position: absolute;
      top: calc(50% - 14px);
      left: 50%;
      font-family: $ff-regular;
      font-size: 100px;
      color: rgba(232, 232, 232, 1);
      transform: translate(-50%, -50%);
      z-index: -1;
    }
  }

  a {
    display: block;
    font-family: $ff-regular;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.6px;
    color: $color-sapphire;
    box-sizing: border-box;
    width: fit-content;
    min-width: 206px;
    text-align: center;
    text-transform: uppercase;
    margin: 0 auto;
    padding: 25px 20px 23px;
    border: 2px solid $color-sapphire;

    &:focus {
      background: $button-gradient;
      color: $color-default-white;
      border: none;
      padding: 26px 22px;
    }

    &:active {
      opacity: 0.6;
    }
  }

  li:hover {
    border: 7px solid $color-default-white;
    border-image: $gradient;
    border-image-slice: 1;
    padding: 50px 16px 61px;

    a {
      background: $button-gradient;
      color: $color-default-white;
      border: none;
      padding: 26px 22px;
    }
  }
}
