.bottom {
  position: relative;
  // background: linear-gradient(to right, $color-sapphire 50%, $color-default-white 50%);

  h2 {
    font-family: $ff-alumniSans-semibold;
    color: $color-sapphire;
    font-size: 32px;
    line-height: 35px;
    margin: 0;
    margin-bottom: 36px;
    letter-spacing: 1px;
  }

  &::before {
    content: "";
    position: absolute;
    width: 50%;
    top: 0;
    right: 0;
    height: 1px;
    background-color: #74819c;
  }
}

.bottom__wrapper {
  display: flex;
  flex-direction: column;
  padding: 0;
}
