.footer {
  background-color: $black-russian;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 15px;
  padding-top: 50px;
  padding-bottom: 49px;
  flex-direction: column;
  text-align: left;
  display: block;

  ul {
    list-style: none;
    padding: 0;
  }

  a {
    color: $color-default-white;

    &:hover,
    &:focus {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.footer__logo {
  display: none;
}

.footer__navigation {
  display: grid;
  grid-template-columns: 1fr 1fr;
  max-width: 780px;
  flex-wrap: wrap;
  height: auto;
  gap: 0 60px;
  margin: 0;
  margin-bottom: 7px;

  a {
    display: block;
    font-family: $ff-regular;
    font-size: 16px;
    line-height: 16px;
    padding: 15px 0;
  }

  li:first-child {
    margin: 0;

    img {
      width: 68px;
      height: 15px;
    }
  }
}

.footer__social {
  display: flex;
  margin: 0 0 0 -4px;

  a {
    display: block;
    font-size: 16px;
    padding: 0 15px 24px 0;
    padding: 5px;
  }
}
