.promo__wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 70px;
  padding-bottom: 70px;
}

.promo__text {
  display: flex;
  max-width: 500px;
  flex-direction: column;
  color: $color-default-white;
  background-color: $color-torch-red;
  padding-top: 50px;
  padding-bottom: 50px;
  padding-left: 44px;
  padding-right: 44px;
  width: 100%;
}

.promo__top {
  display: flex;
  margin-bottom: 10px;

  h2 {
    font-family: $ff-alumniSans-semibold;
    letter-spacing: 2.5px;
    font-size: 48px;
    line-height: 47px;
    width: min-content;
    margin: 0;
    margin-right: 30px;
    text-transform: uppercase;
  }
}

.promo__info {
  // margin-top: 2px;

  h3 {
    display: block;
    font-family: $ff-regular;
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
    text-align: left;
  }

  span {
    font-family: $ff-regular;
    font-size: 24px;
    line-height: 24px;
    font-weight: 700;
  }

  p {
    font-family: $ff-regular;
    font-size: 10px;
    line-height: 15px;
    margin: 0;
  }
}

.promo__date {
  // margin-bottom: 13px;

  h3 {
    margin: 0 0 3px;
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
  }

  span {
    margin-right: 7px;
  }

  div {
    display: flex;
    align-items: center;
    width: min-content;
    border-top: 1px solid $color-default-white;
  }
}

.promo__time {
  h3 {
    margin: 0 0 7px;
  }

  span {
    border-top: 1px solid $color-default-white;
  }
}

.promo__bottom {
  p {
    display: block;
    font-family: $ff-regular;
    font-size: 12px;
    line-height: 18px;
    margin: 0 0 45px;
  }

  a {
    display: block;
    font-size: 10px;
    font-family: $ff-regular;
    line-height: 12px;
    letter-spacing: 0.8px;
    text-transform: uppercase;
    color: $color-default-white;
    text-align: center;
    padding: 16px;
    border: 2px solid $color-default-white;

    &:hover {
      color: $color-sapphire;
      background-color: $color-default-white;
    }

    &:focus {
      color: $color-sapphire;
      background-color: $color-default-white;
      border-image: $button-gradient;
      border-image-slice: 1;
    }
  }
}

.promo__image {
  overflow: hidden;
  height: 292px;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}
