.reviews {
  padding-top: 63px;
  padding-bottom: 70px;

  h2 {
    font-family: $ff-alumniSans-semibold;
    font-size: 49px;
    color: $color-sapphire;
    letter-spacing: 1.5px;
    line-height: 45px;
    margin: 0 0 54px;
    text-align: center;
  }
}

.reviews__wrapper {
  position: relative;
  padding: 0 32px;
}

.reviews__slider {
  width: 100%;
  background-color: $resolution-blue;
}

.reviews__slide {
  display: grid;
  grid-template-columns: auto auto;
  grid-template-rows: auto auto;
  gap: 16px 15px;
  text-align: center;
  color: $color-default-white;
  padding-top: 40px;
  padding-right: 25px;
  padding-bottom: 40px;
  padding-left: 25px;

  h3 {
    font-family: $ff-regular;
    font-size: 20px;
    line-height: 15px;
    text-align: left;
    margin: 0 auto;
  }

  p {
    font-family: $ff-regular;
    font-size: 16px;
    line-height: 22px;
    margin: 0;
    grid-row-start: 2;
    grid-row-end: 3;
    grid-column-start: 1;
    grid-column-end: -1;
  }
}

.reviews__avatar {
  width: 68px;
  height: 68px;
  border-radius: 50%;
  overflow: hidden;
}

