.contacts {
  color: $color-sapphire;
  padding: 15px;
  padding-top: 70px;
  padding-bottom: 70px;

  div {
    display: flex;
    flex-direction: column;
  }

  ul {
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    margin-bottom: 36px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  li:not(:last-child) {
    margin: 0;
    margin-bottom: 30px;
  }

  h3 {
    font-family: $ff-regular;
    font-size: 20px;
    line-height: 28px;
    margin: 0;
    margin-bottom: 20px;
  }

  address,
  p,
  a {
    font-family: $ff-regular;
    font-size: 18px;
    line-height: 18px;
    margin: 0;
    font-style: normal;
  }

  a {
    display: block;
    color: $color-sapphire;

    &:hover,
    &:focus {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }
}
